	module.exports = {"data":{"markdownRemark":{"html":"<p>Az FRT negyedik elektromos versenyautója, az FREC-004 kilenc hónap alatt közel nyolcvan csapattag több mint 31 ezer munkaórájának köszönhetően született meg. Az autó a 2013-ban nagy sikereket elérő Michaela továbbfejlesztése, amely kétéves projektnek indult, így drasztikus változások nem következtek be a váz, a hajtáslánc, a fékrendszer vagy éppen a felfüggesztés terén, inkább a finomításokon volt a hangsúly. A legszembetűnőbb változás a teljes aerodinamikai csomag újragondolása volt, hiszen az előző évben csak a diffúzor került beépítésre, a szárnyak továbbfejlesztésre szorultak. A következő évre azonban egy külön aerodinamika csoport alakult. Több száz szimuláció eredményeként készült el az új konstrukció, amely 72 km/h-nál 1000 N leszorító erő létrehozására képes. Ez közel 20%-os növekedést jelent a korábbi változathoz képest, miközben 10%-kal kisebb légellenállást generál. Ezek az értékek ráadásul tovább javíthatók a szárnyak állásszögének változtatásával, és 130 km/h-nál az autó képes a „plafonon menni”, azaz a leszorító erő ekkor több mint az autó tömegéből adódó súlyerő. A mérnökök jelentős fejlesztéseket végeztek az elektronika terén is, elsősorban az energiaellátó rendszerben és az intelligens hajtásszabályozásban.</p>","timeToRead":1,"excerpt":"Az FRT negyedik elektromos versenyautója, az FREC-004 kilenc hónap alatt közel nyolcvan csapattag több mint 31 ezer munkaórájának…","frontmatter":{"title":"FREC-004","cover":{"relativePath":"cars/004/frec_004.jpg"}},"fields":{"slug":"/frec-004"}}},"pathContext":{"slug":"/frec-004"}}

